<template>

    <div class="auth-form">

        <registation-form-step1
            v-if="loginStep == 1"
            :phoneValue="loginFormData.phone"
            :innValue="loginFormData.innNumber"
            :privacyPolicyCheckboxValue="loginFormData.privacyPolicyChecked"
            @toggle-step-nmb="changeLoginStep(2)"
            @get-error="getErrorStatus"/>

        <registation-form-step-2
            @get-error="getErrorStatus"
            @toggle-step-nmb="changeLoginStep(1)"
            v-if="loginStep == 2"/>

        <div class="form-footer-link">
            <p>Уже зарегистрированы?
                <router-link to="/login" class="link">Вход</router-link>
            </p>
        </div>

        <Popup v-if="errorModal"
               :classes="'error-modal'"
               @close-modal="closeModal()">
            <div class="popup-title">
                <div class="h2">Ошибка</div>
            </div>
            <div class="popup-error-text">
                <p>{{getErrorMessage(errorStatus)}}</p>
                <button class="btn close-modal">Закрыть</button>
            </div>
        </Popup>

    </div>

</template>

<script>

    import RegistationFormStep1 from "@/components/Auth/RegistrationFormStep1"
    import RegistationFormStep2 from "@/components/Auth/RegistrationFormStep2"
    import Popup from "@/components/Blocks/Popup"

    export default {
        name: 'Register',
        components: {
            Popup,
            RegistationFormStep1,
            RegistationFormStep2
        },
        data() {
            return {
                errorModal: false,
                errorStatus: '',
                loginFormData: {
                    phone: '',
                    innNumber: '',
                    privacyPolicyChecked: true
                },
                loginStep: 1,
            }
        },
        methods: {
            changeLoginStep(nmb) {
                this.loginStep = nmb
            },
            getErrorStatus(status) {
                this.errorStatus = status
                this.errorModal = true
            },
            getErrorMessage(status) {
                switch (status) {
                    case 422:
                        return 'Ошибка входных данных'
                    case 412:
                        return 'Попытка отправить код раньше времени'
                    case 401:
                        return 'Смс код не действителен'
                    case 400:
                        return 'Смс код не действителен'
                    default:
                        return 'Неизвестная ошибка. Попробуйте снова.'
                }
            },
            closeModal() {
                this.errorModal = false
                this.errorStatus = ''
            }
        }
    }
</script>
