<template>

    <form @submit.prevent="submitHandler"
          class="auth-form-container">

        <div class="form-title">

            <div class="h2"
                 v-html="$t('registration.title')"/>

            <ul>
                <li v-for="(item, index) in Object.keys($i18n.messages[$i18n.fallbackLocale].registration['descr_list']).length"
                    :key="index"
                    v-html="$t(`registration.descr_list[${index}]`)" />
            </ul>
        </div>

        <fg-input v-model="innValueProp"
                  :mask="'############'"
                  label="ИНН юридического лица"
                  :classes="{invalid: ( $v.innValueProp.$error ? (!$v.innValueProp.required || !$v.innValueProp.minLength) : false ) || innIsReg }"
                  type="text" />

        <div v-if="innIsReg"
             class="form-group-info-text">
            <span class="text-red">Организация с таким ИНН уже зарегистрирована.</span>
            Это ваша организация? <a href="#"
                                     class="link"
                                     @click="accessRecoveryModal = true">Восстановить доступ</a>
        </div>

        <fg-input v-model="phoneValueProp"
                  :mask="'+7 ### ### ## ##'"
                  ref="phoneValueInput"
                  label="Номер телефона"
                  :classes="{invalid: $v.phoneValueProp.$error ? (!$v.phoneValueProp.required || !$v.phoneValueProp.minLength) : false }"
                  type="phone"
                  name="login_phone"
                  @input="addCountryCode"
                  placeholder="+7" />

        <fg-checkbox v-model="privacyPolicyCheckboxValueProp">
            Даю согласие на обработку <a href="#" class="link">персональных данных</a>
        </fg-checkbox>

        <div class="form-submit">
            <button class="btn btn-block btn-xl"
                    :class="load ? 'loading' : ''"
                    :disabled="!privacyPolicyCheckboxValueProp || load"
                    >Получить код</button>
        </div>

        <Popup v-if="accessRecoveryModal"
               ref="accessRecoveryModal"
               @close-modal="accessRecoveryModal = false">

            <div class="popup-title">
                <div class="h2">Восстановление доступа</div>
            </div>

            <div class="popup-body">
                <p v-html="$t('registration.access_recovery')" />
                <button class="btn btn-lg btn-block close-modal"
                        @click="$store.dispatch('restoration')">Войти через гос. услуги</button>
            </div>

        </Popup>

    </form>

</template>

<script>

    import {required, minLength} from 'vuelidate/lib/validators'
    import {mapState} from 'vuex'
    import Popup from "@/components/Blocks/Popup";

    export default {
        components: {
            Popup
        },
        props: {
            phoneValue: [String, Number],
            innValue: [String, Number],
            privacyPolicyCheckboxValue: Boolean,
            loginStep: Number
        },
        validations: {
            phoneValueProp: {required, minLength: minLength(16)},
            innValueProp: {required, minLength: minLength(10)},
        },
        data() {
            return {
                load: false,
                phoneValueProp: this.phoneValue,
                innValueProp: this.innValue,
                privacyPolicyCheckboxValueProp: this.privacyPolicyCheckboxValue,
                accessRecoveryModal: false
            }
        },
        computed: {
            ...mapState({
                innIsReg: state => state.auth.innIsReg
            }),
        },
        methods: {
            changeLoginStep() {
                this.$emit('toggle-step-nmb')
            },
            submitHandler() {
                if (this.$v.$invalid || !this.privacyPolicyCheckboxValueProp) {
                    this.$v.$touch();
                    return
                }
                this.load = true
                let phone = this.$options.filters.phoneFilter(this.phoneValueProp),
                    inn = parseInt(this.innValueProp.replace(/\s+/g, ''));
                this.$store.dispatch('getRegKey', {inn, phone})
                    .then(resp => {
                        if (resp) {
                            this.changeLoginStep()
                        }
                        this.load = false

                    })
                    .catch(() => {
                        this.load = false
                    })
            },
            addCountryCode(e) {
                if ( e.indexOf('+7') !== 0 && e.charAt(0) !== '9' ) {
                    this.phoneValueProp = '+7'
                }
            },
        }
    }
</script>
